<template>
  <div>
    <div class="page-title">
      <h3>Общая статистика игроков</h3>

      <button class="btn waves-effect waves-light btn-small" @click="refresh">
        <i class="material-icons">refresh</i>
      </button>
    </div>
    <div class="row">
      <p>Выберите период:</p>
      <select style="display: block" v-model="choosed" ref="sl">
        <option v-for="d of period" :key="d.id">{{ d.title }}</option>
      </select>
      <p>Поиск по нику:</p>
      <input style="display: block" v-model="nick" />
      <Loader v-if="loading" />
      <p v-else-if="n.length < 1" class="center-align">
        Игрока с таким ником не найдено
      </p>
      <v-table v-else :data="n">
        <thead slot="head">
          <th>Место</th>
          <v-th sortKey="playerName">Ник</v-th>
          <v-th sortKey="totalPlayedGames">Кол-во игр</v-th>
          <v-th sortKey="kills">Убийств</v-th>
          <v-th sortKey="teamkills">Тимкиллов</v-th>
          <v-th sortKey="deaths">Смертей</v-th>
          <v-th sortKey="kdRatio">K/D</v-th>
          <v-th sortKey="totalScore" defaultSort="desc">Счет</v-th>
        </thead>
        <tbody slot="body" slot-scope="{ displayData }">
          <tr v-for="(row, index) in displayData" :key="row.id">
            <td>{{ index + 1 }}</td>
            <td>
              <router-link
                :to="{ name: 'Player', params: { nick: row.playerName } }"
                >{{ row.lastSquadPrefix + row.playerName }}</router-link
              >
            </td>
            <td>{{ row.totalPlayedGames }}</td>
            <td>{{ row.kills }}</td>
            <td>{{ row.teamkills }}</td>
            <td>{{ row.deaths }}</td>
            <td>{{ row.kdRatio }}</td>
            <td>{{ row.totalScore }}</td>
          </tr>
        </tbody>
      </v-table>
    </div>
  </div>
</template>

<script>
export default {
  name: "Players",
  data: () => ({
    n: [],
    loading: true,
    nick: "",
    choosed: "За все время",
    period: [],
  }),
  async mounted() {
    let r = await fetch("rotations_stats.json");
    r = await r.json();
    for (let key in r) {
      let tStartDate = r[key].startDate;
      let tEndDate = r[key].endDate;
      r[key].startDate = new Date(r[key].startDate).toLocaleString("ru", {
        year: "numeric",
        month: "short",
        day: "numeric",
      });
      if (r[key].endDate === null) {
        r[key].endDate = " по настоящий момент.";
      } else {
        r[key].endDate = new Date(r[key].endDate).toLocaleString("ru", {
          year: "numeric",
          month: "short",
          day: "numeric",
        });
      }
      this.period.push({
        id: Number(key),
        title: `${r[key].startDate} - ${r[key].endDate}`,
        endDate: tEndDate,
        startDate: tStartDate,
      });
    }
    this.period.push({
      id: r.length,
      title: "За все время",
    });
    this.period.reverse();
    await this.refresh();
  },
  watch: {
    choosed: function () {
      this.n = [];
      this.refresh();
    },
    nick: function () {
      if (!this.loading) {
        this.n = [];
        this.refresh();
      }
    },
  },
  methods: {
    async refresh() {
      if (this.choosed == this.period[0].title) {
        this.r();
      } else {
        this.rotationTable();
      }
    },
    async r() {
      this.loading = true;
      this.n = [];
      let r = await fetch("stats.json");
      r = await r.json();
      let id = 1;
      for (let key in r.globalStatistics) {
        if (r.globalStatistics[key]) {
          r.globalStatistics[key].id = id;
          id++;
          if (r.globalStatistics[key].lastSquadPrefix === null) {
            r.globalStatistics[key].lastSquadPrefix = "";
          }
          if (this.nick) {
            if (
              r.globalStatistics[key].playerName
                .toLowerCase()
                .indexOf(this.nick.toLowerCase()) != -1
            ) {
              this.n.push(r.globalStatistics[key]);
            }
          } else {
            this.n.push(r.globalStatistics[key]);
          }
        }
      }
      this.loading = false;
    },
    async rotationTable() {
      this.loading = true;
      this.n = [];
      let r = await fetch("rotations_stats.json");
      r = await r.json();
      let id = 1;
      let rotationID = this.$refs.sl.selectedIndex;
      for (let key in r) {
        if (r[key].startDate == this.period[rotationID].startDate) {
          for (let key1 in r[key].stats.global) {
            if (r[key].stats.global[key1].lastSquadPrefix === null) {
              r[key].stats.global[key1].lastSquadPrefix = "";
            }
            if (this.nick) {
              if (
                r[key].stats.global[key1].playerName
                  .toLowerCase()
                  .indexOf(this.nick.toLowerCase()) != -1
              ) {
                this.n.push(r[key].stats.global[key1]);
              }
            } else {
              this.n.push(r[key].stats.global[key1]);
            }
          }
        }
      }
      this.loading = false;
    },
    showPlayer(nick) {
      this.$router.push(`/player/${nick}`);
    },
  },
};
</script>
